import React from 'react';
import { Filter, useStaticListFilterCollection } from '@wix/patterns';

interface StaticCollectionFilterProps {
  filter: Filter<any>;
  collectionData: { id: string; name: string }[];
  component: React.ElementType;
  [key: string]: any;
}

export const StaticCollectionFilter: React.FC<StaticCollectionFilterProps> = ({
  filter,
  collectionData,
  component,
  ...filterProps
}) => {
  const FilterComponent = component;

  const collection = useStaticListFilterCollection(
    filter,
    collectionData ?? [],
  );

  return (
    <FilterComponent filter={filter} collection={collection} {...filterProps} />
  );
};
